import { Bot, User, Copy, Check } from 'lucide-react';
import { Theme } from '@/app/page';

interface ChatMessageProps {
  message: {
    id: string;
    content: string;
    role: 'user' | 'assistant';
    timestamp: Date;
  };
  isCopied: boolean;
  onCopy: () => void;
  theme: Theme;
}

export default function ChatMessage({ message, isCopied, onCopy, theme }: ChatMessageProps) {
  const isUser = message.role === 'user';
  const formattedTime = message.timestamp.toLocaleTimeString([], { 
    hour: '2-digit', 
    minute: '2-digit' 
  });

  const themeConfigs = {
    light: {
      user: {
        bg: 'bg-blue-500',
        text: 'text-white',
        time: 'text-blue-200',
        bubble: 'bg-blue-500 text-white rounded-tr-none',
        copy: 'bg-blue-400 hover:bg-blue-300',
      },
      assistant: {
        bg: 'from-blue-500 to-purple-600',
        text: 'text-gray-900',
        time: 'text-gray-500',
        bubble: 'bg-white border border-gray-200 shadow-sm rounded-tl-none',
        copy: 'bg-gray-100 hover:bg-gray-200',
      },
    },
    dark: {
      user: {
        bg: 'bg-blue-600',
        text: 'text-gray-100',
        time: 'text-blue-300',
        bubble: 'bg-blue-600 text-gray-100 rounded-tr-none',
        copy: 'bg-blue-500 hover:bg-blue-400',
      },
      assistant: {
        bg: 'from-gray-700 to-gray-600',
        text: 'text-gray-100',
        time: 'text-gray-400',
        bubble: 'bg-gray-800 border border-gray-700 shadow-lg rounded-tl-none',
        copy: 'bg-gray-700 hover:bg-gray-600',
      },
    },
    gradient: {
      user: {
        bg: 'bg-gradient-to-r from-indigo-500 to-purple-600',
        text: 'text-white',
        time: 'text-indigo-200',
        bubble: 'bg-gradient-to-r from-indigo-500 to-purple-600 text-white rounded-tr-none',
        copy: 'bg-indigo-400 hover:bg-indigo-300',
      },
      assistant: {
        bg: 'from-indigo-100 to-purple-100',
        text: 'text-gray-900',
        time: 'text-indigo-600',
        bubble: 'bg-gradient-to-r from-white to-indigo-50/80 border border-indigo-100 shadow-sm rounded-tl-none',
        copy: 'bg-indigo-100 hover:bg-indigo-200',
      },
    },
    minimal: {
      user: {
        bg: 'bg-gray-800',
        text: 'text-gray-900',
        time: 'text-gray-600',
        bubble: 'bg-gray-800 text-white rounded-tr-none',
        copy: 'bg-gray-600 hover:bg-gray-500',
      },
      assistant: {
        bg: 'from-gray-200 to-gray-300',
        text: 'text-gray-900',
        time: 'text-gray-500',
        bubble: 'bg-gray-50 border border-gray-200 rounded-tl-none',
        copy: 'bg-gray-200 hover:bg-gray-300',
      },
    },
  };

  const currentTheme = themeConfigs[theme];
  const roleTheme = isUser ? currentTheme.user : currentTheme.assistant;

  return (
    <div className={`flex ${isUser ? 'justify-end' : 'justify-start'} animate-in fade-in duration-300`}>
      <div className={`flex max-w-[85%] md:max-w-[80%] ${isUser ? 'flex-row-reverse' : 'flex-row'}`}>
        {/* Avatar */}
        <div className={`flex-shrink-0 w-10 h-10 rounded-full flex items-center justify-center shadow-sm ${
          isUser ? 'ml-3' : 'mr-3'
        } ${roleTheme.bg}`}>
          {isUser ? (
            <User className="w-5 h-5 text-white" />
          ) : (
            <Bot className="w-5 h-5 text-white" />
          )}
        </div>

        {/* Message bubble */}
        <div className={`relative rounded-2xl px-5 py-4 shadow-sm ${roleTheme.bubble}`}>
          <div className="flex items-center justify-between mb-2">
            <span className={`text-xs font-medium ${roleTheme.time}`}>
              {isUser ? 'You' : 'AI Assistant'}
            </span>
            <span className={`text-xs ml-3 ${roleTheme.time}`}>
              {formattedTime}
            </span>
          </div>
          
          <p className={`whitespace-pre-wrap break-words ${roleTheme.text}`}>
            {message.content}
          </p>
          
          {/* Copy button */}
          <button
            onClick={onCopy}
            className={`absolute -bottom-2 ${
              isUser ? 'left-4' : 'right-4'
            } p-1.5 rounded-full transition-all duration-200 ${roleTheme.copy} shadow-sm`}
            title="Copy message"
          >
            {isCopied ? (
              <Check className="w-3.5 h-3.5 text-green-600" />
            ) : (
              <Copy className="w-3.5 h-3.5 text-gray-600" />
            )}
          </button>
        </div>
      </div>
    </div>
  );
}