import { Palette, Moon, Sun, Zap, Minimize2 } from 'lucide-react';
import { Theme } from '@/app/page';

interface ThemeSelectorProps {
  currentTheme: Theme;
  onThemeChange: (theme: Theme) => void;
}

const themes = [
  { id: 'light', label: 'Light', icon: Sun, color: 'from-yellow-400 to-orange-500' },
  { id: 'dark', label: 'Dark', icon: Moon, color: 'from-gray-800 to-gray-900' },
  { id: 'gradient', label: 'Gradient', icon: Palette, color: 'from-indigo-400 via-purple-400 to-pink-400' },
  { id: 'minimal', label: 'Minimal', icon: Minimize2, color: 'from-gray-300 to-gray-400' },
] as const;

export default function ThemeSelector({ currentTheme, onThemeChange }: ThemeSelectorProps) {
  return (
    <div className="py-4 px-4 md:px-6 max-w-4xl mx-auto w-full">
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center space-x-2">
          <Palette className="w-5 h-5 text-gray-500" />
          <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
            Theme Selector
          </span>
        </div>
        <span className="text-xs text-gray-500 dark:text-gray-400">
          Click to switch
        </span>
      </div>
      
      <div className="flex flex-wrap gap-3">
        {themes.map((theme) => (
          <button
            key={theme.id}
            onClick={() => onThemeChange(theme.id)}
            className={`flex items-center space-x-3 px-4 py-3 rounded-xl transition-all duration-300 flex-1 min-w-[140px] ${
              currentTheme === theme.id
                ? 'ring-2 ring-offset-2 ring-opacity-50 shadow-lg transform scale-[1.02]'
                : 'hover:shadow-md hover:scale-[1.01]'
            } ${
              theme.id === 'dark' 
                ? 'bg-gray-800 text-gray-100 ring-gray-400' 
                : theme.id === 'gradient'
                ? 'bg-gradient-to-r from-indigo-50 to-purple-50 text-gray-800 ring-indigo-300'
                : 'bg-white text-gray-800 ring-gray-300'
            }`}
          >
            <div className={`w-10 h-10 rounded-lg flex items-center justify-center bg-gradient-to-br ${theme.color}`}>
              <theme.icon className="w-5 h-5 text-white" />
            </div>
            <div className="text-left">
              <div className="font-medium">{theme.label}</div>
              <div className="text-xs opacity-70">
                {currentTheme === theme.id ? 'Active' : 'Switch'}
              </div>
            </div>
          </button>
        ))}
      </div>
    </div>
  );
}