import { Send, Paperclip, Mic, Image as ImageIcon } from 'lucide-react';
import { Theme } from '@/app/page';

interface ChatInputProps {
  input: string;
  setInput: (value: string) => void;
  isLoading: boolean;
  onSend: () => void;
  onKeyPress: (e: React.KeyboardEvent) => void;
  theme: Theme;
}

export default function ChatInput({ 
  input, 
  setInput, 
  isLoading, 
  onSend, 
  onKeyPress,
  theme 
}: ChatInputProps) {
  const themeConfigs = {
    light: {
      container: 'bg-white border-gray-200 shadow-sm',
      input: 'bg-transparent text-gray-900 placeholder-gray-500',
      button: 'text-gray-500 hover:text-gray-700 hover:bg-gray-100',
      send: 'from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700',
      hint: 'text-gray-500',
    },
    dark: {
      container: 'bg-gray-800 border-gray-700 shadow-lg',
      input: 'bg-transparent text-gray-100 placeholder-gray-400',
      button: 'text-gray-400 hover:text-gray-200 hover:bg-gray-700',
      send: 'from-blue-600 to-purple-700 hover:from-blue-700 hover:to-purple-800',
      hint: 'text-gray-400',
    },
    gradient: {
      container: 'bg-gradient-to-r from-white to-indigo-50/50 border-indigo-100 shadow-sm',
      input: 'bg-transparent text-gray-900 placeholder-indigo-400',
      button: 'text-indigo-500 hover:text-indigo-700 hover:bg-indigo-100',
      send: 'from-indigo-500 to-purple-600 hover:from-indigo-600 hover:to-purple-700',
      hint: 'text-indigo-500',
    },
    minimal: {
      container: 'bg-gray-50 border-gray-200',
      input: 'bg-transparent text-gray-900 placeholder-gray-500',
      button: 'text-gray-500 hover:text-gray-700 hover:bg-gray-200',
      send: 'from-gray-800 to-gray-600 hover:from-gray-900 hover:to-gray-700',
      hint: 'text-gray-500',
    },
  };

  const currentTheme = themeConfigs[theme];

  return (
    <div className={`rounded-2xl border p-3 ${currentTheme.container}`}>
      <div className="flex items-center space-x-2">
        <button className={`p-2 rounded-xl transition-colors ${currentTheme.button}`}>
          <Paperclip className="w-5 h-5" />
        </button>
        
        <button className={`p-2 rounded-xl transition-colors ${currentTheme.button}`}>
          <ImageIcon className="w-5 h-5" />
        </button>
        
        <div className="flex-1 relative">
          <textarea
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={onKeyPress}
            placeholder="Type your message here..."
            className={`w-full p-4 pr-12 border-0 focus:ring-0 resize-none max-h-40 min-h-[60px] rounded-xl ${currentTheme.input}`}
            rows={1}
            disabled={isLoading}
          />
          
          <button className={`absolute right-3 top-1/2 transform -translate-y-1/2 p-2 rounded-xl transition-colors ${currentTheme.button}`}>
            <Mic className="w-5 h-5" />
          </button>
        </div>
        
        <button
          onClick={onSend}
          disabled={!input.trim() || isLoading}
          className={`p-4 bg-gradient-to-r ${currentTheme.send} text-white rounded-xl hover:shadow-md disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 shadow-sm`}
        >
          <Send className="w-5 h-5" />
        </button>
      </div>
      
      <div className={`mt-3 text-xs px-3 ${currentTheme.hint}`}>
        Press <kbd className="px-2 py-1 bg-gray-100 dark:bg-gray-700 rounded text-xs">Enter</kbd> to send â€¢ 
        <kbd className="px-2 py-1 bg-gray-100 dark:bg-gray-700 rounded text-xs mx-2">Shift</kbd> + 
        <kbd className="px-2 py-1 bg-gray-100 dark:bg-gray-700 rounded text-xs mx-1">Enter</kbd> for new line
      </div>
    </div>
  );
}